export const apiProxyRequest = ({ apiUrl, contentType, size, searchTerm = '', page = 1, sort = '{"title": 1}' }) =>
  fetch(`${apiUrl}?limit=${size}&skip=${(page - 1) * size}&sort=${encodeURIComponent(sort)}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      search: {
        query: {
          bool: {
            must: [
              {
                query_string: {
                  fields: ['title'],
                  query: `${searchTerm.split(' ').map(term => `${term}~2`).join(' ')}`,
                  fuzziness: 2,
                  default_operator: 'AND'
                }
              },
              { match: { _type: contentType } }
            ]
          }
        }
      },
      _fields: {
        title: 1,
        _doc: 1
      }
    })
  })
    .then((result) => {
      return result.json();
    });
